@import '../../base';

.full-height {
  height: 100%;
}

.company-product-container,
.header-desc-with-tabview-container {
  background-color: white;
  border: solid 1px $color-line;
  margin-bottom: 24px;
  margin-top: 10px;

  @media screen and (min-width: 690px) {
    margin-top: 0;
  }

  .left-container,
  .mid-container,
  .right-container {
    padding: 0;
    width: 33%,
  }

  .left-container {
    max-width: 196px;
    border-right: solid 1px $color-line;
    display: flex;

    .motor-company-container {
      padding: 12px;
      padding-bottom: 20px;
  
      width: 100%;
      display: flex;
      flex-direction: column;
      
      
      .company-name {
        font-size: 10px;
        font-weight: $font-weight-bold;
        color: $color-body;
        text-align: center;
        margin: auto;
      }
    }

    @media screen and (min-width: 690px) {
      margin-top: 0;

      .motor-company-container {
        padding: 31px;
        display: flex;
        justify-content: center;
        align-items: center;

        .company-logo-container {
          width: 100px;
          height: 100px;
          max-width: 100px;

          .company-logo {
            width: 100%;
            height: auto;
          }
        }
  
        .company-name {
          margin: 0;
          font-size: 20px;
        }
      }
    }
  }

  .company-logo-container {
    display: flex;
    justify-content: center;
    align-items: center;

    width: 70px;
    height: 70px;
    max-width: 70px;

    .company-logo {
      width: 100%;
      height: auto;
    }
  }

  .mid-container {
    padding: 10px;
    padding-top: 0;

    ul {
      padding-left: 20px;

      li {
        padding: 5px 0;
        font-size: 10px;
        font-weight: $font-weight-normal;
        color: $color-body;
      }
    }

    .more {
      &:hover {
        color: $color-hover-alt;
        outline: none;
      }

      color: $color-hover;
      font-size: 10px;
      font-weight: $font-weight-normal;
    }

    .why {
      font-size: $font-size-big * 0.5;
      font-weight: $font-weight-heavy;
      color: $color-title;
      margin-top: 10px;
    }

    @media screen and (min-width: 690px) {
      padding: 31px;
      padding-bottom: 0;

      .why {
        font-size: 20px;
        font-weight: $font-weight-bold;
      }

      ul {
        margin-bottom: 0;
  
        li {
          font-size: $font-size-small;
          font-weight: $font-weight-light;
        }
      }

      .more {
        font-size: $font-size-smallest;
        font-weight: $font-weight-normal;
      }
    }
  }

  .right-container {
    flex: 0;
    padding-right: 10px;

    p {
      text-align: center;
    }

    .cost {
      font-size: 20px;
      font-weight: $font-weight-heavy;
      color: $color-title;
    }

    .year {
      font-size: $font-size-big;
      font-weight: $font-weight-normal;
      color: $color-title;
    }

    .unit {
      font-size: $font-size-big * 0.5;
    }

    @media screen and (min-width: 690px) {
      padding: 31px;
      padding-bottom: 20px;
      margin-top: -20px;

      .cost {
        font-size: 44px;
      }

      .unit {
        font-size: $font-size-regular;
      }
    }
  }

  .button-medium-color {
    @media screen and (max-width: 560px) {
      min-width: 80px !important;
      min-height: 36px !important;
      padding: 10px 0 !important;
  
      p {
        font-size: 10px !important;
      }
    }
  }
}

.header-desc-with-tabview-container {
  &:hover {
    box-shadow: 0 10px 20px 0 rgba(0, 0, 0, 0.1);
    background-color: #ffffff;
    border-style: solid;
    border-image-source: linear-gradient(102deg, $color-hover, $color-body);
    border-image-slice: 1;
  }
}

.enquiry-container {
  @media screen and (max-width: 690px) {
    margin-top: 15px !important;
  }
}
