@import '../../../base';

.header-desc-with-tabview-container {
  .button-sub-text {
    margin-top: 8px;
    font-size: $font-size-smallest;
    font-weight: $font-weight-heavy;
    color: $color-hover;
    text-align: center;
  }

  .right-main {
    flex: 1;
    min-width: 66%;

    .top-container {
      padding: 15px;
      border-bottom: solid 1px #C8E0E4;
      display: flex;
      align-items: center;

      .name-container {
        flex: 1;
        display: flex;
        flex-direction: column;
        justify-content: center;

        .name-logo {
          display: flex;
          align-items: center;
        }
      }
    }

    .company-logo-container  {
      @media screen and (max-width: 575px) {
        justify-content: flex-start;
        margin-right: 10px;
      }
    }
  }

  .price-container {
    margin-left: 10px;
    p {
      text-align: center;
    }

    .cost {
      font-size: 44px;
      font-weight: $font-weight-heavy;
      color: $color-title;
    }

    .unit {
      font-size: $font-size-big;
      font-weight: $font-weight-normal;
      color: $color-title;
    }

    @media screen and (max-width: 690px) {
      .cost {
        font-size: 20px;
      }

      .unit {
        font-size: $font-size-big * 0.5;
      }
    }
  }

  .desc {
    font-size: $font-size-smallest + $font-size-tiny;
    font-weight: $font-weight-bold;
    color: $color-body;

    @media screen and (max-width: 690px) {
      font-size: $font-size-tiny;
      margin-top: 5px;
    }
  }

  .tag-label {
    margin-bottom: 0;

    @media screen and (max-width: 767px) {
      margin-top: 0;

      span,
      p {
        font-size: $font-size-tiny;
      }
    }  
  }

  .button-medium-flat-green {
    min-width: 0 !important;
    max-width: 100%;
    width: 165px;
    margin-top: 10px;

    @media screen and (max-width: 575px) {
      padding: 0 10px;
      min-height: 48px !important;
      p {
        font-size: $font-size-tiny;
      }
    }
  }
}
