@import '../../../base';

.voucher-tag-container {
  margin-bottom: 24px;
  // padding-left: 15px;
  display: flex;
  align-items: center;

  @media screen and (max-width: 767px) {
    margin-top: 12px;
  }

  button,
  span {
    margin-left: 8px;
    font-weight: $font-weight-semibold;
    font-size: $font-size-smallest;

    color: $color-input-form-landing-desc2;
  }

  button {
