@import '../../base';

.motor-container {
  .landing-form-img-container {
    .img-1 {
      height: 50%;
    }
  }

  .promo-outer-extra {
    margin-top: -40px;

    @media screen and (max-width: 576px) {
      margin-top: -60px !important;
      .desc {
        max-width: 100%;
      }
    }

    @media screen and (max-width: 400px) {
      margin-top: -80px !important;
      .desc {
        max-width: 100%;
        margin-top: -180px !important;
      }
    }
  }

  .promo-img-extra {
    @media screen and (max-width: 576px) {
      margin-top: -50px;
      .desc {
        max-width: 100%;
      }
    }

    @media screen and (max-width: 400px) {
      margin-top: 50px;
      .desc {
        max-width: 100%;
      }
    }
  }
}


.inner-form-container {
  max-width: 1036px;
  background-color: white;
  margin: auto;
  // margin-top: 20px;
  padding: 0 15px;

  .form-bottom-margin {
    margin-bottom: 80px;
  }

  .company-product-container {
    .header-desc-with-price-container {
      border-bottom: solid 1px $color-line !important;
    }
  }

  .content-container {
    padding: 50px 25px;

    .why {
      font-size: 20px;
      font-weight: $font-weight-heavy;
      color: $color-title;
    }

    ul {
      padding: 0px 20px;
      margin-bottom: 50px;
      
      li {
        font-size: $font-size-small;
        font-weight: $font-weight-normal;
        color: $color-body;
        line-height: normal;
      }
    }

    @media screen and (max-width: 560px) {
      padding: 50px 0px;

      .why {
        font-size: $font-size-small;
        padding: 0px 10px;
      }

      ul {
        padding: 0px 30px;

        li {
          font-size: $font-size-smallest;
        }
      }
    }
  }
}

.inner-form-no-tabs {
  margin-top: 0 !important;

  .tab-container {
    display: none;
  }

  form {
    .tab-container {
      display: flex !important;
    }
  }

  .detail-not-changable-container {
    .info-container:nth-of-type(3) {
      border-top: solid 1px $color-line !important;
    }
  }
}

.inner-form-extra-padding {
  margin-top: 60px;

  @media screen and (max-width: 576px) {
    margin-top: 40px;
  }
}
