@import '../../../base';

.enquiry-container {
  width: 100%;
  min-height: 288px;
  border: solid 1px $color-line;

  @media screen and (max-width: 960px) {
    margin-bottom: 80px;
  }
  .upper-container {
    width: 100%;
    min-height: 120px;
    background-image: linear-gradient(96deg, $color-hover, $color-body);
    padding: 36px 30px;
    display: flex;

    a,
    .left-text {
      margin: auto;
      margin-left: 0;
      max-width: 500px;
      font-size: $font-size-regular;
      font-weight: $font-weight-light;
      line-height: normal;
      color: white;
    }

    .right-text {
      justify-content: flex-end;
      margin: auto;
      margin-right: 0;
      display: flex;

      img {
        margin-right: 20px;
      }
      span {
        font-size: $font-size-big * 2;
        font-weight: $font-weight-bold;
        color: white;
      }
    }

    @media screen and (max-width: 960px) {
      flex-direction: column;

      .left-text,
      .right-text {
        margin: auto;
      }

      .left-text {
        text-align: center;
      }
    }

    @media screen and (max-width: 560px) {
      padding: 16px 20px;

      .left-text {
        max-width: 217px;
        font-size: $font-size-big * 0.5;
      }

      .right-text {
        margin-top: 20px;
        img {
          width: 21px;
          height: 21px;
          margin-top: 5px;
        }

        span {
          font-size: 20px;
        }
      }
    }
  }

  .lower-container {
    display: flex;
    flex-direction: column;
    padding: 5px;
    padding-bottom: 20px;

    .lower-main {
      font-size: $font-size-big;
      font-weight: $font-weight-normal;
      color: $color-hover;
      line-height: 1.54;
      letter-spacing: -0.4px;
      text-align: center;
    }

    .lower-sub {
      font-size: $font-size-regular;
      font-weight: $font-weight-light;
      color: $color-caption;
      text-align: center;
      line-height: 1;
    }

    @media screen and (max-width: 560px) {
      .lower-main {
        margin-top: 20px;
        font-size: $font-size-smallest;
      }

      .lower-sub {
        font-size: $font-size-big * 0.5;
      }
    }
  }
}