@import '../../../base';

.change-form-container {
  display: flex;
  
  .edit-detail-container{
    position: relative;
    display: flex;
    flex-wrap: wrap;
    padding-left: 20px;
    margin: 30px 0;
    flex-direction: column;
    .edit-detail-inner-container {
      display: flex;
    }

    .edit-detail-header {
      font-size: $font-size-smallest;
      font-weight: $font-weight-semibold;
      color: $color-title;
      margin-bottom: 10px;
    }

    .edit-detail-label-container,
    .edit-detail-value-container {
      display: flex;
      flex-direction: column;
    }
  
    .edit-detail-label-container {
      font-size: $font-size-smallest;
      font-weight: $font-weight-semibold;
      color: $color-body;
      margin-right: 27px;
    }
  
    .edit-detail-value-container {
      font-size: $font-size-smallest;
      font-weight: $font-weight-semibold;
      color: $color-title;

      .detail-value {
        position: relative;
        
        .tooltip-button {
          width: 16px;
          height: 16px;
          border: solid 1px $color-hover;
          border-radius: 50%;
          display: flex;
          justify-content: center;
          align-content: center;
          position: absolute;
          top: 3px;
          right: -44px;
          &:hover {
            cursor: pointer;
          }
  
          p {
            height: 100%;
            margin-top: 0;
            text-align: center;
            color: $color-hover;
            font-size: 10px;
            font-weight: $font-weight-light;
          }
        }
      }

      .white {
        color: white;
      }
    }
  }

  .edit-container {
    width: 30px;
    height: 30px;
    position: absolute;
    display: flex;
    right: -50px;
    justify-content: center;
    align-items: center;
    margin-top: -6px;
    top: 0;
    img {
      width: 30px;
      height: 30px;
      // filter: $color-filter-hover;

      &:hover {
        filter: brightness(80%);
      }

      &:active {
        width: 30px;
        height: 30px;
      }
    }
  }

  .tooltip-text {
    text-align: center;
    padding: 10px 0;
    font-size: $font-size-smallest;
    font-weight: $font-weight-semibold;
    color: $color-title;
  }
}
